import { Injectable } from "@angular/core";
import { Http, Response } from '@angular/http';
import 'rxjs/Rx'
import { Observable } from "rxjs/Rx";
import { Stock } from "./Stock";
@Injectable()


export class StockService{


    constructor(private httpService:Http)
    {
    }

    storeStock(newStockDetails:Stock[])
    {
        return this.httpService.put('https://angular-http-testing-7cd4c.firebaseio.com/stocks.json',newStockDetails);
    }



    getStocks()
    {
        return this.httpService.get('https://angular-http-testing-7cd4c.firebaseio.com/stocks.json')
        .map(
            (respone:Response)=>{
            return respone.json();
            }
        )
        .catch(
            (error)=>{
                console.log(error);
                return Observable.throw('Unable to reach server');
            }
        );
    }

    getStockPrice(stockName:string)
    {
        let url = "https://www.alphavantage.co/query?function=TIME_SERIES_INTRADAY&symbol=NSE:"+stockName+"&interval=1min&outputsize=compact&apikey=V2MEKS17X9YVD5JA";
        return this.httpService.get(url)
        .map(
            (res:Response)=>{
                return res.json();
            }
        )
        .catch(
            (error)=>{
                console.log(error);
                return Observable.throw('Unable to reach server');
            }
        );
    }
}



