import { Component, OnInit ,AfterViewInit, Input} from '@angular/core';
import { NgForm } from '@angular/forms';
import { StockService } from '../stock.service';
import { Stock } from '../Stock';
import * as Chart from 'chart.js'
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabase,AngularFireList } from 'angularfire2/database';
// import firebase = require('firebase');

@Component({
  selector: 'app-stocks',
  templateUrl: './stocks.component.html',
  styleUrls: ['./stocks.component.css']
})

export class StocksComponent implements OnInit,AfterViewInit{

  constructor(private stockService: StockService, private fdb:AngularFireDatabase) { }

  ID: string;
  BuyPrice: number;
  Quantity: number;
  CurrentPrice: number;
  listOfStocks: Stock[] = [];
  refreshStockPrice: any;
  listOfStockIDs: string[] = [];
  listOfStockValues: number[] = [];
  PieChart: any;
  realTimeStockValues: any;
  sharesList: AngularFireList<Stock>;
  share:Stock = {
    ID:"QWER",
    BuyPrice:22,
    CurrentPrice:33,
    Quantity:4
  }
  ngAfterViewInit(): void {
    this.getStocks();
    setInterval(()=>
    {
      if(this.listOfStocks)
      {
        for (let stock of this.listOfStocks) {
          this.getStockPrice(stock.ID);
        }
      }
    },60000);
  }

  ngOnInit() {

    //this.fdb.list<Stock>('/Shares').push(this.share);

    // this.realTimeStockValues = setInterval(()=>
    // {
    //   this.temp+=5;
    // },2000);
    // this.realTimeStockValues();
  }

  getStocks() {
    this.stockService.getStocks()
      .subscribe(
        (data: any[]) => {
          if (data) {
            this.listOfStocks = data;
            this.refreshStocksDetails();
          }
        });
  }

  getShares(){

  }

  refreshStocksDetails() {
    if(this.listOfStocks)
    {
      this.listOfStockIDs = [];
      this.listOfStockValues = [];
      for (let stock of this.listOfStocks) {
        this.getStockPrice(stock.ID);
        this.listOfStockIDs.push(stock.ID);
        this.listOfStockValues.push(stock.BuyPrice*stock.Quantity);
      }
      this.updatePieChart();
    }
  }

  onAddStockToList(newStock: NgForm) {
    this.listOfStocks.push(newStock.value);
    this.stockService.storeStock(this.listOfStocks)
      .subscribe(
        (res) => {
          this.refreshStocksDetails();
        });
  }

  getStockPrice(stockID: string) {
    this.stockService.getStockPrice(stockID)
      .subscribe((data: any[]) => {
        console.log('service calll', data);
        if(data!=undefined || data==null)
        {
          let lastRefreshed = data['Meta Data']['3. Last Refreshed'];
          let price = data['Time Series (1min)'][lastRefreshed]['4. close'];
          let index = this.listOfStocks.findIndex((stock) => stock.ID == stockID);
          this.listOfStocks[index].CurrentPrice = price;
        }
        else
        {
          console.log("response is null");
        }
      });
  }

  updatePieChart()
  {
    this.PieChart = new Chart('pieChart', {
      type: 'pie',
      data: {
          labels: this.listOfStockIDs,
          datasets: [{
              data: this.listOfStockValues,
              backgroundColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(255, 199, 132, 1)',
                  'rgba(0, 221, 157,1)'
              ],
              borderWidth: 1
          }]
      }
    });
  }

  editStock(index)
  {
    console.log(event)
  }

  removeStock(index)
  {
    this.listOfStocks.splice(index,1);
    this.refreshStocksDetails();
  }
}



